$sizes: 14, 16, 18, 20, 36, 40,70 ;
@each $size in $sizes {
    &.fs-#{$size} {
        font-size: #{$size}px;
        &.t-responsive{
            @media screen and (max-width: 768px) {
                font-size: calc(#{$size}px * 0.75 );
            }
        }
        &.c-responsive{
            @media screen and (max-width: 768px) {
                text-align: center;
            }
        }
    }
}
$weights: lighter,normal,bold,bolder;
@each $weight in $weights {
    &.fw-#{$weight} {
        font-weight: $weight;
    }
} 
$spacings: 0,5,10,15,20,30,40;
@each $spacing in $spacings {
    &.s-t-#{$spacing} {
        margin-top: #{$spacing}px;
    }
    &.s-b-#{$spacing} {
        margin-bottom: #{$spacing}px;
    }
}
$lns :( "1":1,
        "1-15":1.15,
        "1-25":1.25,
        "1-33":1.33,
        "1-5":1.5,
        "1-75":1.75,
        "1-90":1.90,
        "2":2);
@each $key,$val in $lns {
    &.ln-#{$key} {
        line-height: $val;
    }

}
$aligns: left,center,right;
@each $align in $aligns {
    &.ta-#{$align} {
        text-align: $align;
    }
}